import React from 'react'
import WeatherSunnyIcon from 'mdi-react/WeatherSunnyIcon'
import WeatherNightIcon from 'mdi-react/WeatherNightIcon'

// TODO: Fix vertical lineup.
// TODO: Fix jump to left on resize
// TODO: Add functional dark mode using is-dark class

const NightLight = props => {
  const setToggle = e => {
    props.toggle(e.target.checked)
  }

  return (
    <div className={props.theme ? 'control is-dark has-text-white' : 'control'} style={{maxWidth: '100px'}}>
      {/*<div className="level-item">*/}
        <label htmlFor="nightLightSwitch">
          <span className="icon">
            <WeatherSunnyIcon/>
          </span>
        </label>
      {/*</div>*/}
      {/*<div className="level-item">*/}
        <input id="nightLightSwitch" type="checkbox" name="nightLightSwitch" className="switch is-rounded is-success"
               onClick={setToggle}/>
        <label htmlFor="nightLightSwitch">
          <span className="icon">
            <WeatherNightIcon/>
          </span>
        </label>
      {/*</div>*/}
    </div>
  )
}

export default NightLight
